___CSS_LOADER_EXPORT___.push([module.id, `.typing-dots {
    display: flex;
    gap: 4px;
}

.typing-dots span {
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: gray;
    border-radius: 50%;
    animation: bounce 1.2s infinite ease-in-out;
}

.typing-dots span:nth-child(2) {
    animation-delay: 0.2s;
}

.typing-dots span:nth-child(3) {
    animation-delay: 0.4s;
}

.typing-spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid gray;
    border-top: 3px solid transparent;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}


@keyframes bounce {

    0%,
    80%,
    100% {
        transform: scale(0);
    }

    40% {
        transform: scale(1);
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/indicatorStyle.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,qBAAqB;IACrB,UAAU;IACV,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,2CAA2C;AAC/C;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,qBAAqB;IACrB,WAAW;IACX,YAAY;IACZ,sBAAsB;IACtB,iCAAiC;IACjC,kBAAkB;IAClB,kCAAkC;AACtC;;AAEA;IACI;QACI,uBAAuB;IAC3B;;IAEA;QACI,yBAAyB;IAC7B;AACJ;;;AAGA;;IAEI;;;QAGI,mBAAmB;IACvB;;IAEA;QACI,mBAAmB;IACvB;AACJ","sourcesContent":[".typing-dots {\r\n    display: flex;\r\n    gap: 4px;\r\n}\r\n\r\n.typing-dots span {\r\n    display: inline-block;\r\n    width: 8px;\r\n    height: 8px;\r\n    background-color: gray;\r\n    border-radius: 50%;\r\n    animation: bounce 1.2s infinite ease-in-out;\r\n}\r\n\r\n.typing-dots span:nth-child(2) {\r\n    animation-delay: 0.2s;\r\n}\r\n\r\n.typing-dots span:nth-child(3) {\r\n    animation-delay: 0.4s;\r\n}\r\n\r\n.typing-spinner {\r\n    display: inline-block;\r\n    width: 20px;\r\n    height: 20px;\r\n    border: 3px solid gray;\r\n    border-top: 3px solid transparent;\r\n    border-radius: 50%;\r\n    animation: spin 1s linear infinite;\r\n}\r\n\r\n@keyframes spin {\r\n    from {\r\n        transform: rotate(0deg);\r\n    }\r\n\r\n    to {\r\n        transform: rotate(360deg);\r\n    }\r\n}\r\n\r\n\r\n@keyframes bounce {\r\n\r\n    0%,\r\n    80%,\r\n    100% {\r\n        transform: scale(0);\r\n    }\r\n\r\n    40% {\r\n        transform: scale(1);\r\n    }\r\n}"],"sourceRoot":""}]);
